import { fromJS } from 'immutable';
import { createAction, handleActions } from 'redux-actions';

const addMemo = createAction('STORAGE/NEW_MEMO')
const storeNote = createAction('STORAGE/STORE_NOTE')
const newDot = createAction('STORAGE/NEW_DOT')
const setDotData = createAction('STORAGE/SET_DOT_DATA')
const removeDot = createAction('STORAGE/REMOVE_DOT')
const setAnswer = createAction('STORAGE/SET_ANSWER')

const initialState = fromJS({
  memos: [],
});

const reducer = handleActions({
  [addMemo]: (state, { payload }) => state.update('memos', list => list.push(fromJS(payload).set('createdAt', new Date()))),
  [storeNote]:  (state, { payload: { name, value, index } }) => state.setIn(['memos', index, 'notes', name], value),
  [newDot]: (state, { payload: { position, memoIndex } }) => state.updateIn(['memos', memoIndex, 'dots'], list => list.push(fromJS({ position }))),
  [setDotData]: (state, { payload: { index, pain, duration, memoIndex } }) =>
    state.updateIn(['memos', memoIndex, 'dots', index], dot => dot.set('pain', pain).set('duration', duration)),
  [removeDot]: (state, { payload: { memoIndex, index } }) => state.removeIn(['memos', memoIndex, 'dots', index]),
  [setAnswer]: (state, { payload: { name, value, memoIndex } }) => state.setIn(['memos', memoIndex, 'answers', name], value),
}, initialState);

export {
  addMemo,
  storeNote,
  newDot,
  setDotData,
  removeDot,
  setAnswer,
};

export default reducer;
