/**
 * Combine all reducers in this file and export the combined reducers.
 */

import { combineReducers } from 'redux';
import fromPairs from 'lodash/fromPairs';
import zip from 'lodash/zip';
import { createResponsiveStateReducer } from 'redux-responsive';

import creating from '../containers/MemoPage/creatingReducer'
import storing from '../containers/MemoPage/storingReducer'
import setting from '../containers/TestPage/setAnswerReducer'
import rate from '../containers/StarRater/rateReducer'
import { breakpoints } from '../components/ThemeProvider/theme';

const names = [
  'xxs',
  'xs',
  'sm',
  'md',
  'lg',
  'xl',
];

/**
 * Creates the main reducer with the dynamically injected ones
 */
export default function createReducer(injectedReducers) {
  return combineReducers({
    browser: createResponsiveStateReducer(fromPairs(zip(names, breakpoints.map((bp) => parseInt(bp, 10) - 1)))),
    creating,
    storing,
    setting,
    rate,
    ...injectedReducers,
  });
}
