import merge from 'lodash/merge';
import range from 'lodash/range';
import mapValues from 'lodash/mapValues';

import colors from 'open-color/open-color.json';

const emToPx = (em) => `${em * 16}px`;

export const breakpoints = [22, 36, 48, 62, 90, 120].map(emToPx);
export const containerWidth = [22, 36, 46, 72].map(emToPx);
export const mobileOrDesktop = (mobile, desktop) => [mobile, null, null, desktop];

const generateFade = (r, g, b) => range(10, 100, 10)
  .reduce((fade, opacity) => merge(fade, { [opacity]: `rgba(${[r, g, b, opacity / 100].join()})` }), {});

const flatternColors = mapValues(colors, (listOfColors) => listOfColors[5]);

const font = 'Arial, "Noto Sans CJK TC", "Noto Sans TC", sans-serif';
const serifFont = '"Noto Serif CJK TC", "Noto Serif TC", serif';

const white = '#fff';
const black = '#000';
const darkBlue = '#525252';
const titleBlue = '#22195B';
const gray = '#C7C7C7';
const primary = '#FF8267';
const secondary = 'green';
const danger = '#ff4017';
const questionBg = '#F4F4F8';
const noteBg = '#B4B2CC';
const orange = '#FF8267';
const bodyBgOrange = '#FFE4DE'
const bgOrange = '#F6CFC6';
const lightOrange = '#FF9983';
const titleBgOrange = '#FFE4DE';
const lightBgOrange = '#FFFBF7';
const colorOrange = '#fee3dd';
const lightPurple = '#B4B2CC';
const lightBgPurple = '#D9D8E5';
const purple = '#7974AF';

export default {
  colors: {
    ...flatternColors,
    white,
    gray,
    black,
    orange,
    darkBlue,
    titleBlue,
    questionBg,
    colorOrange,
    bgOrange,
    lightOrange,
    bodyBgOrange,
    titleBgOrange,
    lightBgOrange,
    lightPurple,
    lightBgPurple,
    purple,
    noteBg,
    text: '#525252',
    primary: primary,
    primaryHover: primary,
    danger,
    secondary: flatternColors[secondary],
    secondaryHover: colors[secondary][9],
    secondaryVariations: colors[secondary],
    variations: colors,
    fade: {
      white: generateFade(255, 255, 255),
      black: generateFade(0, 0, 0),
    },
  },
  breakpoints,
  containerWidth,
  font,
  serifFont,
  headerHeight: '3em',
  footerHeight: '2em',
  mobileOrDesktop,
  zOrder: range(4).map((i) => 10 ** i),
};
